import { Component, OnChanges, OnInit, OnDestroy, Input } from '@angular/core';
import { interval, Subscription } from 'rxjs';
import { CampaignStatisticService } from 'src/app/services/campaign-statistic.service';
import { CAMPAIGN_STATS_INTERVAL } from './../../constants/constants'

@Component({
  selector: 'app-campaign-statistic',
  template: `
    <!-- <h2 class="heading">Campaign Statistic</h2>  -->
    <mat-grid-list [cols]="5" rowHeight="84px" [gutterSize]="'16px'">
      <mat-grid-tile [colspan]="1" [rowspan]="1">
        <!-- <mat-card class="card-progress" *ngIf="isLoading">
          <mat-progress-bar mode="indeterminate"></mat-progress-bar>
        </mat-card> -->
        <mat-card class="all-campaign-widget">
          <div class="statistic-text">
            <h6>{{runningCampaignCount}}</h6>
            <span>Campaigns</span>
          </div>
          <div class="statistic-icons">
            <img src="assets/img/champains-statistic.png" alt="">
          </div>
        </mat-card>
      </mat-grid-tile>
      <mat-grid-tile [colspan]="1" [rowspan]="1">
        <mat-card class="all-campaign-widget">
          <div class="statistic-text">
            <h6>{{onlineAgents}}</h6>
            <span>Agents</span>
          </div>
          <div class="statistic-icons">
            <img src="assets/img/agents-statistic.png" alt="">
          </div>
        </mat-card>
      </mat-grid-tile>
      <mat-grid-tile [colspan]="1" [rowspan]="1">
        <mat-card class="all-campaign-widget">
          <div class="statistic-text">
            <h6>{{freeChannels}}</h6>
            <span>Free Channels</span>
          </div>
          <div class="statistic-icons">
            <img src="assets/img/channels-statistic.png" alt="">
          </div>
        </mat-card>
      </mat-grid-tile>
      <mat-grid-tile [colspan]="1" [rowspan]="1">
        <mat-card class="all-campaign-widget">
          <div class="statistic-text">
            <h6>{{inboundOnHold}}</h6>
            <span>Inbound Hold</span>
          </div>
          <div class="statistic-icons">
            <img src="assets/img/inbound-statistic.png" alt="">
          </div>
        </mat-card>
      </mat-grid-tile>

      <mat-grid-tile [colspan]="1" [rowspan]="1">
        <mat-card class="all-campaign-widget">
          <div class="statistic-text">
            <h6>{{totalHits}}</h6>
            <span>Total Hits</span>
          </div>
          <div class="statistic-icons">
            <img src="assets/img/inbound-statistic.png" alt="">
          </div>
        </mat-card>
      </mat-grid-tile>
    </mat-grid-list>
  `,
  styles: [`
    .all-campaign-widget{
      display: flex;
      justify-content: space-between;
      width: 100%;
      border-radius: 14px;
      border: 1px solid #F0F1F4;
      box-shadow: none;
      padding: 18px 32px;
    }

    .all-campaign-widget h6{
        font-weight: 600;
        font-size: 23px;
        margin-bottom: 6px;
    }
    .all-campaign-widget span{
        font-size: 12px;
        color: #8A91A8;
    }

    .statistic-text{
      float: left;
    }
    .statistic-icons{
        float: right;
        margin-top: 6px
    }

    .statistic-icons img{
      width: 30px
    }

    .statistic-text h6{
      height: 20px
    }
  `]
})
export class CampaignStatisticComponent implements OnChanges, OnInit, OnDestroy {

  freeChannels!: number
  inboundOnHold!: number
  onlineAgents!: number
  runningCampaignCount!: number
  totalHits!: number
  isLoading: boolean = true
  statisticInterval!: Subscription;
  @Input() isAction: boolean = false

  constructor(private campaignStatisticService: CampaignStatisticService) { }

  ngOnChanges(){
    if(this.isAction){
      this.statisticInterval.unsubscribe()
      this.getDashBoardStatsLive()
    }
  }

  ngOnInit(): void {
    this.getDashBoardStats()
    this.getDashBoardStatsLive()
  }

  getDashBoardStatsLive(){
    this.statisticInterval = interval(CAMPAIGN_STATS_INTERVAL).subscribe((x) => {
      this.getDashBoardStats()
    })
  }

  getDashBoardStats(){
    this.campaignStatisticService.getDashBoardStats({
      "customerID": localStorage.getItem('customerID'),
      "userID": localStorage.getItem('userID')
    }).subscribe((res) => {
      if (res.baseResult.result === 'Success') {
        this.isLoading = false;
        this.freeChannels = res.baseResult.value.freeChannels
        this.inboundOnHold = res.baseResult.value.inboundOnHold
        this.onlineAgents = res.baseResult.value.onlineAgents
        this.runningCampaignCount = res.baseResult.value.runningCampaignCount
        this.totalHits = res.baseResult.value.totalHits
      }
    }, (err) => {
      this.isLoading = false
      console.log(err)
    })
  }

  ngOnDestroy() {
    this.statisticInterval.unsubscribe()
  }

}
