import { AfterViewInit, Component, ViewChild, OnInit, OnDestroy } from '@angular/core';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { DashboardService } from './../../services/dashboard.service'
import { IDropdownSettings } from 'ng-multiselect-dropdown';
import { interval, Subscription } from 'rxjs';
import { CAMPAIGN_STATS_INTERVAL } from './../../constants/constants'
import { DispositionService } from 'src/app/services/disposition.service';


@Component({
  selector: 'app-dispositions',
  templateUrl: './dispositions.component.html', 
  styleUrls: ['./dispositions.component.scss']
})
export class DispositionsComponent implements OnInit {

  
  isLoading: boolean = true
  filteredValue: string = ''
  campaignID: number = 0
  dataObj: any;
  displayedColumns: string[] = ['key', 'value'];
  columnsToDisplay: string[] = this.displayedColumns.slice();
  dispositionInterval!: Subscription;
  dataSource!: MatTableDataSource<any>;
  dropdownList = [];

  dropdownSettings = {
    singleSelection: true,
    idField: 'campaignId',
    textField: 'name',
    disabledField: 'item_disable',
    selectAllText: 'Select All',
    unSelectAllText: 'UnSelect All',
    itemsShowLimit: 1,
    allowSearchFilter: true,
    value: 'all',
    closeDropDownOnSelection: true
  };

  selectedItems = [];
  changeValues = [];
  extraColumns: string[] = [];

  @ViewChild(MatPaginator) paginator!: MatPaginator;
  @ViewChild(MatSort) sort!: MatSort;
  
  constructor(private dispositionService: DispositionService) { }

  ngOnInit(): void {
    var start = new Date();
    start.setHours(0, 0, 0, 0);

    var end = new Date();
    end.setHours(23, 59, 59, 999);

    this.dataObj = {
      "customerID": this.getItem('customerID'),
      "userID": this.getItem('userID'),
      "campaignID": this.campaignID,
      "startDate": start.toISOString(),
      "endDate": end.toISOString()
    }

    this.getAllAgents()
    this.getAllCampaigns()
    this.getAgentsLive()
  }

  getAgentsLive() {
    this.dispositionInterval = interval(CAMPAIGN_STATS_INTERVAL).subscribe(() => {
      this.getAllAgents()
      this.getAllCampaigns()
    });
  }

  ngAfterViewInit() { }

  getAllAgents() {
    this.dispositionService.getAgentDispositionStats(this.dataObj).subscribe(res => {
      console.log(res)
      this.setData(res.baseResult.length ? res.baseResult[0].userDispositionStats : [])
    }, (err) => {
      this.isLoading = true
      console.log(err)
    })
  }

  getAllCampaigns(){
    this.dispositionService.getAllCampaigns().subscribe(res => {
      console.log(res)
      this.dropdownList = res
    }, (err) => {
      console.log(err)
    })
  }

  setData(baseResult: any){
    console.log(baseResult)
    this.isLoading = false
    this.dataSource = new MatTableDataSource(baseResult)
    this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;
    this.filteredValue ? this.dataSource.filter = this.filteredValue.trim().toLowerCase() : this.dataSource.filter = ''
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.filteredValue = filterValue
    this.dataSource.filter = filterValue.trim().toLowerCase()

    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  public onItemSelect(item: any) {
    this.isLoading = true
    this.dataObj['campaignID'] = item.campaignId
    this.resetInterval()
    this.getAllAgents()
  }

  resetInterval(){
    this.dispositionInterval.unsubscribe()
    this.getAgentsLive()
  }

  getItem(key: string) {
    return localStorage.getItem(key)
  }
}
