import { AfterViewInit, Component, ViewChild, OnInit, OnDestroy } from '@angular/core';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { DashboardService } from './../../services/dashboard.service'
import { IDropdownSettings } from 'ng-multiselect-dropdown';
import { interval, Subscription } from 'rxjs';
import { CAMPAIGN_STATS_INTERVAL } from './../../constants/constants'
import { AgentsService } from 'src/app/services/agents.service';

export interface UserData {
  id: string;
  name: string;
  progress: string;
}

@Component({
  selector: 'app-agents',
  templateUrl: './agents.component.html',
  styleUrls: ['./agents.component.scss']
})
export class AgentsComponent implements OnInit {

  displayedColumns: string[] = ['name', 'agentStatus', 'breakType', 'lastStatusTime', 'totalHits', 'totalCalls', 'campaignName', 'listName', 'callType', 'customerName', 'customerNumber'];
  agents: any = []
  filteredAgents: any = []
  dataSource!: MatTableDataSource<UserData>;
  isLoading: boolean = true
  // agentStatus: boolean = false
  // isAction: boolean = false
  indexes: string = ""
  filteredValue: string = ''
  tooltipText: string = "Show Transfer Agents"
  agentInterval!: Subscription;
  showOnlyTransferAgents: boolean = false

  selectedItems = ['totalCalls', 'campaignName', 'listName', 'callType', 'customerName', 'customerNumber'];
  changeValues = [];
  extraColumns: string[] = ['totalCalls', 'campaignName', 'listName', 'callType', 'customerName', 'customerNumber', 'talkTime', 'breakTime', 'wrapUpTime', 'averageTalkTime', 'averageWrapUpTime', 'totalInboundCalls', 'totalTransfers'];
  dropdownList = [
    { val: 'totalCalls', text: 'Total Calls' },
    { val: 'campaignName', text: 'Campaign Name' },
    { val: 'listName', text: 'List Name' },
    { val: 'callType', text: 'Call Type' },
    { val: 'customerName', text: 'Customer Name' },
    { val: 'customerNumber', text: 'Customer Phone' },
    { val: 'talkTime', text: 'Talk Time' },
    { val: 'breakTime', text: 'Break Time' },
    { val: 'wrapUpTime', text: 'Wrap Up Time' },
    { val: 'averageTalkTime', text: 'Avg. Talk Time' },
    { val: 'averageWrapUpTime', text: 'Avg. WrapUp Time' },
    { val: 'totalInboundCalls', text: 'Inbound' },
    { val: 'totalTransfers', text: 'Transfers' },
  ];

  dropdownSettings = {
    singleSelection: false,
    idField: 'val',
    textField: 'text',
    disabledField: 'item_disable',
    selectAllText: 'Select All',
    unSelectAllText: 'UnSelect All',
    itemsShowLimit: 1,
    allowSearchFilter: false,
    value: 'all'
  };

  @ViewChild(MatPaginator) paginator!: MatPaginator;
  @ViewChild(MatSort) sort!: MatSort;

  constructor(private agentsService: AgentsService) { }

  ngOnInit(): void {
    this.getAllAgents()
    this.getAgentsLive()
  }

  getAgentsLive() {
    this.agentInterval = interval(CAMPAIGN_STATS_INTERVAL).subscribe(() => {
      this.getAllAgents()
    });
  }

  ngAfterViewInit() { }

  getAllAgents() {
    let obj = {
      "customerID": this.getItem('customerID'),
      "userID": this.getItem('userID')
    }
    this.agentsService.getAllAgentStats(obj).subscribe(res => {
      this.setData(res.baseResult)
    }, (err) => {
      this.isLoading = true
      console.log(err)
    })
  }

  setData(baseResult: any){
    console.log(baseResult);
    this.isLoading = false
    this.indexes = ''
    this.agents = baseResult.filter((e: { isTransferUser: boolean; }) => {
      return e.isTransferUser === this.showOnlyTransferAgents
    });
    this.dataSource = new MatTableDataSource(this.agents)
    this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;
    this.filteredValue ? this.dataSource.filter = this.filteredValue.trim().toLowerCase() : this.dataSource.filter = ''
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.filteredValue = filterValue
    this.dataSource.filter = filterValue.trim().toLowerCase()

    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  getItem(key: string) {
    return localStorage.getItem(key)
  }

  showAllAgents() {
    this.isLoading = true
    this.getAllAgents()
    this.resetInterval()
    this.showOnlyTransferAgents ? this.tooltipText = 'Show All Agents' : this.tooltipText = 'Show Transfer Agents'
  }

  public onItemSelect(item: any) {
    this.displayedColumns.push(item.val);
  }
  public onDeSelect(item: any) {
    var index = this.displayedColumns.indexOf(item.val);
    if (index !== -1) {
      this.displayedColumns.splice(index, 1);
    }
  }

  public onSelectAll(items: any) {
    this.displayedColumns = (this.displayedColumns.slice(0, 5)).concat(this.extraColumns)
  }

  public onDeSelectAll(items: any) {
    this.displayedColumns = this.displayedColumns.slice(0, 5)
  }

  resetInterval() {
    this.agentInterval.unsubscribe()
    this.getAgentsLive()
  }

  ngOnDestroy() {
    this.agentInterval.unsubscribe()
  }
}
