import { Injectable } from '@angular/core';
import { HttpHeaders, HttpClient } from '@angular/common/http';
import { API_URL } from '../constants/constants';
import { retry, catchError } from 'rxjs/operators';
import { Observable, throwError } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class DashboardService {

  constructor(private http: HttpClient) { }

  private headers = new HttpHeaders({ 'Content-Type': 'application/json' });

  getAllCampaigns(data: any): Observable<any> {
    return this.http.post<any>(API_URL + '/api/Campaigns/GetAllCampaignStats', data, { headers: this.headers })
      .pipe(
        retry(1),
        catchError(this.handleError)
      );
  }

  StartCampaign(data: any): Observable<any> {
    return this.http.post<any>(API_URL + '/api/Campaigns/StartCampaign', data, { headers: this.headers })
      .pipe(
        retry(1),
        catchError(this.handleError)
      );
  }

  StopCampaign(data: any): Observable<any> {
    return this.http.post<any>(API_URL + '/api/Campaigns/StopCampaign', data, { headers: this.headers })
      .pipe(
        retry(1),
        catchError(this.handleError)
      );
  }

  PauseCampaign(data: any): Observable<any> {
    return this.http.post<any>(API_URL + '/api/Campaigns/PauseCampaign', data, { headers: this.headers })
      .pipe(
        retry(1),
        catchError(this.handleError)
      );
  }

  ResumeCampaign(data: any): Observable<any> {
    return this.http.post<any>(API_URL + '/api/Campaigns/ResumeCampaign', data, { headers: this.headers })
      .pipe(
        retry(1),
        catchError(this.handleError)
      );
  }

  GetFreeChannelCount (data: any): Observable<any> {
    console.log(data);
    
    return this.http.post<any>(API_URL + '/api/Campaigns/GetFreeChannelCount ', data, { headers: this.headers })
      .pipe(
        retry(1),
        catchError(this.handleError)
      );
  }

  handleError(error: any) {
    return throwError(error.message || error);
  }
  
}