import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { LoginService } from './../services/login.service'

@Component({
  selector: 'app-pages',
  template: `
    <ng-container>
      <mat-sidenav-container class="pages-container">
        <mat-sidenav #sidenav mode="side" opened class="pages-sidenav"
                    [fixedInViewport]="options.value.fixed" [fixedTopGap]="options.value.top"
                    [fixedBottomGap]="options.value.bottom">
          <div class="logo">
            <img class="menu-icon" src="assets/img/logo.png" width="100">
          </div>

          <mat-accordion>
            <mat-expansion-panel [expanded]="step === 0" (opened)="setStep(0)">
              <mat-expansion-panel-header routerLinkActive="active">
                <mat-panel-title>
                  <img class="menu-icon" src="assets/img/menu-icon-campaign.svg"> Dashboards
                </mat-panel-title>
              </mat-expansion-panel-header>
              <div class="sub-menu">
                <p><a routerLinkActive="active" routerLink="/pages/campaigns">Campaigns</a></p>
                <p><a routerLinkActive="active" routerLink="/pages/agents">Agents</a></p>
                <p><a routerLinkActive="active" routerLink="/pages/dispositions">Dispositions</a></p>
              </div>
            </mat-expansion-panel>
          </mat-accordion>

          <mat-accordion>
            <mat-expansion-panel [expanded]="step === 1" (opened)="setStep(1)">
              <mat-expansion-panel-header routerLinkActive="active">
                <mat-panel-title>
                  <img class="menu-icon" src="assets/img/menu-icon-campaign.svg"> Campaigns
                </mat-panel-title>
              </mat-expansion-panel-header>
              <div class="sub-menu">
                <p>Callback</p>
                <p>History</p>
                <p>Last Result</p>
              </div>
            </mat-expansion-panel>
          </mat-accordion>

          <mat-accordion>
            <mat-expansion-panel [expanded]="step === 2" (opened)="setStep(2)">
              <mat-expansion-panel-header>
                <mat-panel-title>
                  <img class="menu-icon" src="assets/img/list.svg"> List
                </mat-panel-title>
              </mat-expansion-panel-header>
              <div class="sub-menu">
                <p>Import</p>
              </div>
            </mat-expansion-panel>
          </mat-accordion>

          <mat-accordion>
            <mat-expansion-panel [expanded]="step === 3" (opened)="setStep(3)">
              <mat-expansion-panel-header>
                <mat-panel-title>
                  <img class="menu-icon" src="assets/img/dns.svg"> DNC
                </mat-panel-title>
              </mat-expansion-panel-header>
              <div class="sub-menu">
                <p>Import</p>
              </div>
            </mat-expansion-panel>
          </mat-accordion>
          <mat-accordion>
            <mat-expansion-panel hideToggle>
              <mat-expansion-panel-header>
                <mat-panel-title>
                  <img class="menu-icon" src="assets/img/user.svg"> Users
                </mat-panel-title>
              </mat-expansion-panel-header>
            </mat-expansion-panel>
          </mat-accordion>

          <mat-accordion>
            <mat-expansion-panel hideToggle>
              <mat-expansion-panel-header>
                <mat-panel-title>
                  <img class="menu-icon" src="assets/img/groups.svg"> Groups
                </mat-panel-title>
              </mat-expansion-panel-header>
            </mat-expansion-panel>
          </mat-accordion>

          <mat-accordion>
            <mat-expansion-panel hideToggle>
              <mat-expansion-panel-header>
                <mat-panel-title>
                  <img class="menu-icon" src="assets/img/chat.svg"> Chat
                </mat-panel-title>
              </mat-expansion-panel-header>
            </mat-expansion-panel>
          </mat-accordion>

          <mat-accordion>
            <mat-expansion-panel [expanded]="step === 4" (opened)="setStep(4)">
              <mat-expansion-panel-header>
                <mat-panel-title>
                  <img class="menu-icon" src="assets/img/sms.svg"> SMS
                </mat-panel-title>
              </mat-expansion-panel-header>
              <div class="sub-menu">
                <p>Long Code</p>
                <p>SMS Template</p>
              </div>
            </mat-expansion-panel>
          </mat-accordion>

          <mat-accordion>
            <mat-expansion-panel hideToggle>
              <mat-expansion-panel-header>
                <mat-panel-title>
                  <img class="menu-icon" src="assets/img/reports.svg"> Reports
                </mat-panel-title>
              </mat-expansion-panel-header>
            </mat-expansion-panel>
          </mat-accordion>

          <!-- <mat-selection-list [multiple]="false">
            <mat-list-option>
              Reports
            </mat-list-option>
          </mat-selection-list> -->


          <!-- <mat-accordion>
            <mat-expansion-panel [expanded]="step === 5" (opened)="setStep(5)">
              <mat-expansion-panel-header>
                <mat-panel-title>
                  Settings
                </mat-panel-title>
              </mat-expansion-panel-header>
              <div class="sub-menu">
                <p>General</p>
                <p>Time Zones</p>
                <p>Channels</p>
                <p>Services</p>
                <p>Email Templates</p>
                <p>Staus Colors</p>
                <p>Break Type</p>
              </div>
            </mat-expansion-panel>
          </mat-accordion> -->

        </mat-sidenav>

        <mat-sidenav-content [formGroup]="options">

        <mat-toolbar class="pages-header">
            <img id="group" src="assets/img/group.svg" (click)="sidenav.toggle()" alt="">
            <div>
              <div class="user" [matMenuTriggerFor]="menu">
                <span class="material-icons md-dark" style="font-size: 32px; color: #595A5A">person</span>
              </div>
              <mat-menu #menu="matMenu">
                <button mat-menu-item (click)="logout()">Logout</button>
              </mat-menu>
            </div>
        </mat-toolbar>
          
        <div class="contents">
          <router-outlet></router-outlet>
        </div>
          
        </mat-sidenav-content>
          
        </mat-sidenav-container>

      <!-- <mat-toolbar class="pages-footer">Footer</mat-toolbar> -->
    </ng-container>
    `,
  styles: [`
    /* .pages-container {
        position: absolute;
        top: 60px;
        bottom: 0;
        left: 0;
        right: 0;
      } */

      .pages-sidenav {
        display: flex;
        align-items:
        center;
        justify-content: center;
        width: 220px;
      }

      .pages-header {
        justify-content: space-between
      }

      .pages-footer {
        position: fixed;
        bottom: 0;
        left: 0;
        right: 0;
      }

      .contents{
        padding: 20px;
      }

      .sub-menu{
        padding-left: 26px
      }

      mat-sidenav-content{
        background: #FDFDFF
      }

      #group{
        cursor: pointer
      }
      
      .mat-drawer-side{
        box-shadow: 0px 100px 82px rgba(177, 177, 177, 0.12), 0px 12.5216px 10.2677px rgba(177, 177, 177, 0.06);
        border-right: none;
      }

      .mat-expansion-panel:not([class*=mat-elevation-z]){
        box-shadow: none
      }

      .mat-toolbar{
        background: #FFFFFF
      }

      mat-expansion-panel{
        color: #8A91A8
      }

      .mat-panel-title, .sub-menu p, .mat-expansion-panel-header{
        font-size: 13px
      }

      .menu-icon{
        margin-right: 8px
      }

      .mat-expansion-panel-header{
        padding: 0 16px;
      }

      .logo{
        padding: 16px 0 8px;
        text-align: center
      }

      .logo h3{
        margin-bottom: 0;
        font-weight: bold
      }

      .mat-expansion-panel-header.mat-expanded{
        height: 48px !important
      }

      .mat-drawer-content{
        height: 100vh;
      }

      .user{
        width: 30px;
        height: 30px;
        border-radius: 50%;
        cursor: pointer
      }
    `]
})
export class PagesComponent implements OnInit {

  showFiller = false;
  panelOpenState = true;

  ngOnInit(): void {
  }

  options: FormGroup;

  constructor(fb: FormBuilder, private loginService: LoginService) {
    this.options = fb.group({
      bottom: 0,
      fixed: true,
      top: 0
    });
  }

  step = 0;

  setStep(index: number) {
    this.step = index;
  }

  nextStep() {
    this.step++;
  }

  prevStep() {
    this.step--;
  }

  shouldRun = [/(^|\.)plnkr\.co$/, /(^|\.)stackblitz\.io$/].some(h => h.test(window.location.host));

  logout(){
    this.loginService.logout()
  }

}
